<template>
	<view class="main">
		<view class="hand">
			<image src="../../static/tabBarimg/dbleft.png" @click="blackbtn"></image>
		</view>
		<view class="show">
			<view class="sone">
				<view class="sonefoot">
					<view class="sfone">
						<view class="sfoneleft">
							<view class="sfoneleftfirst">{{this.projectName}}-{{this.areaName}}</view>
							<view :class="beitype=='0'?'sfoneleftsun':'sfoneleftsun1'" @click="bbtn">备注：{{this.marks}}</view>
						</view>
						<image src="../../static/tabBarimg/up.png" v-show="beitype==1&&this.marks.length>18" @click="bbtn"></image>
						<image src="../../static/tabBarimg/down.png" v-show="beitype==0&&this.marks.length>18" @click="bbtn"></image>
						<view class="sfoneright" @click="shuxin">
							刷新
						</view>
					</view>
					<view class="sftwo">
						<view class="sftworight">
							<view class="srbtn" @click="scan">添加设备</view>
						</view>
					</view>
				</view>
			</view>
			<view class="stwo">
				<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
				<van-list v-model="loading" :offset="300" :finished="finished" finished-text="没有更多了" @load="loadMore">
				<van-cell v-for="(item, index) in blist" :key="index" >
					<view class="stwotap">
						<view class="stwotapleft">
							<view class="stwotapone">
								<view class="stwotaponeleft">
									设备号：{{item.deviceId}}
								</view>
							</view>
							<view class="stwotapone">
								<view class="stwotaponeleft">
									创建时间：{{item.createTime}}
								</view>
							</view>
							<view class="stwotapone">
								<view class="stwotaponeleft">
									设备厂商：<span v-show="item.deviceFactory==0">福特</span>
								</view>
								<view class="stwotaponeleft">
									出入类型：<span v-show="item.outIn==0">入</span>
									<span v-show="item.outIn==1">出</span>
									<span v-show="item.outIn==2">出入</span>
								</view>
							</view>
						</view>
						
						<view class="stwotapright">
							<view class="stwotaprightfirst" @click="upbtn(item.id)">设置二维码</view>
							<view class="stwotaprightfirst" style="background-color: red;" @click="delbtn(item.id)">删除</view>
						</view>
					</view>
				</van-cell>
				</van-list>
				</van-pull-refresh>	
			</view>
		</view>
		<!-- 费率显示 -->
		<van-popup v-model="ashow">
			<view class="sfour">
				<view class="stwohand">
					添加设备<image src="../../static/tabBarimg/close.png" @click="showclose"></image>
				</view>
				<view class="stwofoot">
					<view class="kshow">
						<view class="kone">
							<input placeholder="设备编号" v-model="deviceId" />
						</view>
						<view class="ktwo">
							<van-collapse v-model="activeNames">
								<van-collapse-item :title=reason name="1" :title-class="classtap">
									<van-radio-group v-model="radio" @change="gobtn">
										<van-radio name="福特">福特</van-radio>
									</van-radio-group>
								</van-collapse-item>
							</van-collapse>
					
						</view>
						<view class="ktwo">
							<van-collapse v-model="activeNames1">
								<van-collapse-item :title=reason1 name="1" :title-class="classtap1">
									<van-radio-group v-model="radio1" @change="gobtn1">
										<van-radio name="出">出</van-radio>
										<van-radio name="入">入</van-radio>
										<van-radio name="出入">出入</van-radio>
									</van-radio-group>
								</van-collapse-item>
							</van-collapse>
											
						</view>
						<view class="ktwo">
							<view class="kbtn" @click="addbtn">
								添加
							</view>
						</view>
					</view>
				</view>
			</view>
		</van-popup>
	</view>
</template>

<script>
	 
	import Toast from '../../wxcomponents/vant/toast/toast';
	import Dialog from '../../wxcomponents/vant/dialog/dialog';
	export default {
		components: {},
		mounted() {
			
		},

		data() {
			return {
				projectName:'',
				areaName:'',
				marks:'',
				marks1:'',
				beitype:0,
				areaId:'',//小区id
				blist:[],//设备列表
				loading: false,
				finished: false,
				refreshing: false,
				pageNum: 1, //页码
				pageSize: 10, //每页显示的条数
				ashow:false,
				activeNames: [''],
				radio: '', //设备厂商
				reason: '设备厂商',
				classtap:'tclass',
				activeNames1: [''],
				radio1: '', //门禁类型
				reason1: '门禁类型',
				classtap1:'tclass',
				deviceId:'',//设备号
			}
		},
		onShow() {
			this.firstone=false
		},
		onLoad(options) {
			this.projectName=options.projectName
			this.areaId=options.areaId
			this.areaName=options.areaName
			this.marks=options.marks
			this.marks1=options.marks
		},
		methods: {
			//返回上一页
			blackbtn() {
				uni.navigateBack()
			},
			shuxin(){
				this.blist=[];
				this.onRefresh();
			},
			bbtn(){
				if(this.beitype==0){
					this.beitype=1
				}else if(this.beitype==1){
					this.beitype=0
				}
			},
			scan(){
				this.ashow=true
			},
			showclose(){
				this.ashow=false
			},
			// 设备列表分页
			loadMore() {
				if (this.refreshing) {
				  this.blist = [];
				  this.refreshing = false;
				}
				let data = {
					areaId:this.areaId,
					pageNum:this.pageNum,
					pageSize:this.pageSize,
				}
				this.loading = true;
				this.$base.request1('manager/parkingEqupment/list', 'GET', data)
					.then(res => {
						this.loading = false;
						if(res.data.rows&&res.data.rows.length>0){
							var arr=res.data.rows
							this.blist = this.blist.concat(arr);
							this.pageNum += 1;
						}else{
							this.finished = true;
						}
					})
					.catch(err => {
				
					})
			},
			onRefresh() {
			     // 清空列表数据
			     this.finished = false;
				 this.pageNum=1
			     // 重新加载数据
			     // 将 loading 设置为 true，表示处于加载状态
			     this.loading = true;
			     this.loadMore();
			},
			gobtn() {
				this.reason = this.radio
				this.activeNames = ['']
				this.classtap='tclass1'
			},
			gobtn1() {
				this.reason1 = this.radio1
				this.activeNames1 = ['']
				this.classtap1='tclass1'
			},
			addbtn(){
				var deviceFactory=0
				if(this.radio=='福特'){
					deviceFactory=0
				}
				var outIn=0
				if(this.radio1=='入'){
					outIn=0
				}else if(this.radio1=='出'){
					outIn=1
				}else if(this.radio1=='出入'){
					outIn=2
				}
				if(this.deviceId==''){
					this.$toast.fail('请输入设备编号')
					return
				}
				if(this.radio==''){
					this.$toast.fail('请选择设备厂商')
					return
				}
				if(this.radio1==''){
					this.$toast.fail('请选择门禁类型')
					return
				}
				
				let data={
					areaId:this.areaId,
					deviceId:this.deviceId,
					deviceFactory:deviceFactory,
					outIn:outIn
				}
				uni.showLoading({
					title: '请稍候',
					mask: true
				})
				this.$base.request1('manager/parkingEqupment/add', 'POST', data)
				.then(res => {
					uni.hideLoading()
					if(res.data.code==200){
						this.$toast.success('添加成功')
						this.ashow=false
						this.shuxin()
					}else{
						this.$toast.fail('添加失败,'+res.data.msg)
					}
					
				})
				.catch(err => {
			
				})
			},
			upbtn(id){
				let data={
					id:id
				}
				uni.showLoading({
					title: '请稍候',
					mask: true
				})
				this.$base.request1('manager/parkingEqupment/sendQrCode', 'POST', data)
				.then(res => {
					uni.hideLoading()
					if(res.data.code==200){
						this.$toast.success('设置成功')
					}else{
						this.$toast.fail('设置失败,'+res.data.msg)
					}
					
				})
				.catch(err => {
							
				})
			},
			delbtn(id){
				let data={
					id:id
				}
				uni.showLoading({
					title: '请稍候',
					mask: true
				})
				this.$base.request1('manager/parkingEqupment/remove', 'DELETE', data)
				.then(res => {
					uni.hideLoading()
					if(res.data.code==200){
						this.$toast.success('删除成功')
						this.shuxin()
					}else{
						this.$toast.fail('删除失败,'+res.data.msg)
					}
					
				})
				.catch(err => {
							
				})
			}
			
		}
	}
</script>

